        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="企业信息配置" name="first"
            >企业信息配置</el-tab-pane
          >
          <el-tab-pane label="话机列表" name="second">话机列表</el-tab-pane>
          <el-tab-pane label="号码管理" name="third">号码管理</el-tab-pane>
          <el-tab-pane label="坐席管理" name="fourth">坐席管理</el-tab-pane>
          <el-tab-pane label="呼叫计费配置" name="adwv">
            <div class="call" v-for="(item, index) in value" :key="index">
              <div>子企业呼转配置</div>
              <br />

              <div>
                <span>呼转特殊配置</span
                ><el-switch
                  v-model="item.state"
                  active-color="#13ce66"
                  inactive-color="#ccc"
