<script>
import { upsuperstatus } from "../api/http";
export default {
  data() {
    return {
      activeName: "adwv",
      value: [{ state: true }, { state: false }, { state: true }],
    };
  },
  //创建后
  created() {},
  //挂载后
  mounted() {},
  props: ["data", "lidata"],
  //事件监听器
  methods: {
    clickupsuperstatus(val) {
      this.$confirm(
        `${
          val.status == 1
            ? "停用超级企业,其下子企业也全部停用！"
            : "是否要启用超级企业！"
        }`,
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      )
        .then(() => {
          upsuperstatus({
            id: val.id,
            status: val.status == 1 ? 0 : 1,
          }).then((res) => {
            // console.log(res);
            this.$message({
              showClose: true,
              message: res.msg,
              type: res.code == "0" ? "success" : "error",
            });
            if (res.code == "0") {
              this.$emit("sharing");
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
  },
};
</script>

