<template>
  <div id="enterprise">
    <div>
      <SuperEnterpriseList
        @sharing="sharings"
        ref="SuperEnterpr"
      ></SuperEnterpriseList>
    </div>
    <div>
      <SuperEnterpriseListManagement
        :data="data"
        @sharing="sharingx"
        :lidata="lidata"
      >
        <div class="tap-left">
          <ul>
            <li :style="lidata ? listyle : ''" @click="lidata = 1">
              超级企业管理
            </li>
            <li :style="lidata ? '' : listyle" @click="lidata = 0">
              子企业管理
            </li>
          </ul>
        </div>
      </SuperEnterpriseListManagement>
    </div>
  </div>
</template>

<script>
import SuperEnterpriseList from "../../components/SuperEnterpriseList.vue";
import SuperEnterpriseListManagement from "../../components/SuperEnterpriseListManagement.vue";
export default {
  data() {
    return {
      data: "",
      lidata: 1,
      listyle: {
        "background-color": "rgb(34,127,235)",
        color: "#fff",
        border: "1px solid rgb(34,127,235)",
      },
    };
  },
  //创建后
  created() {},
  //挂载后
  mounted() {},
  components: { SuperEnterpriseList, SuperEnterpriseListManagement },
  //事件监听器
  methods: {
    sharingx() {
      this.$refs.SuperEnterpr.getsuperesmen();
    },
    sharings(val) {
      this.data = val;
      // console.log(this.data);
    },
  },
};
</script>

<style scoped lang="less">
.tap-left {
  ul {
    display: flex;
    li {
      padding: 8px 10px;
      border: 1px solid #b2b2b2;
      color: #868686;
    }
  }
}
#enterprise {
  display: flex;
  // justify-content: space-between;
}
ul li {
  list-style: none;
}
</style>
