<template>
  <div>
    <div id="EnterpriseSearch">
      <p>超级企业列表<span>(122)</span></p>
      <br />
      <div class="EnterpriseSearchinput">
        <el-input
          placeholder="请输入内容"
          v-model="input"
          @input="getsearchesmen()"
        >
        </el-input>
        <div>&nbsp;</div>
        <el-button
          type="primary"
          icon="el-icon-plus"
          @click="dialogFormVisible = true"
          plain
        ></el-button>
      </div>
    </div>
    <div id="SuperEnterpriseList">
      <ul>
        <li
          v-for="(item, index) in getsuperesdata"
          :key="index"
          @click="sharing(index)"
        >
          <span :style="item.status ? 'color: #15ff00;' : ''">·</span>
          {{ item.ename }}
        </li>
      </ul>
    </div>

    <el-dialog title="添加" :visible.sync="dialogFormVisible">
      <el-form :model="form" ref="form" label-width="130px">
        <el-form-item
          label="超级企业名称"
          prop="ename"
          :rules="[
            { required: true, message: '请输入超级企业名称', trigger: 'blur' },
          ]"
        >
          <el-input v-model="form.ename"></el-input>
        </el-form-item>
        <el-form-item
          label="ip域名"
          prop="ipstr"
          :rules="[
            { required: true, message: '请输入ip域名', trigger: 'blur' },
          ]"
        >
          <el-input v-model="form.ipstr">
            <template slot="append">.emenisnin.com</template>
          </el-input>
        </el-form-item>
        <el-form-item
          label="超级管理员账号"
          prop="adminname"
          :rules="[
            {
              required: true,
              message: '请输入超级管理员账号',
              trigger: 'blur',
            },
          ]"
        >
          <el-input v-model="form.adminname"></el-input>
        </el-form-item>
        <el-form-item
          label="邮箱"
          prop="adminemil"
          :rules="[
            { required: true, message: '请输入邮箱地址', trigger: 'blur' },
            {
              type: 'email',
              message: '请输入正确的邮箱地址',
              trigger: ['blur', 'change'],
            },
          ]"
        >
          <el-input v-model="form.adminemil"></el-input>
        </el-form-item>
        <el-form-item label="最大座席数">
          <el-input v-model="form.maxnum"></el-input>
        </el-form-item>
        <el-form-item label="最大登录并发数">
          <el-input v-model="form.maxconcurrency"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('form')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getsuperes, getsearches, addsuper } from "../api/http.js";
export default {
  data() {
    return {
      zhunagt: 0,
      input: "",
      getsuperesdata: [],
      dialogFormVisible: false,
      form: {
        ename: "111",
        ipstr: "1111111",
        adminname: "1111111",
        adminemil: "1213@163.com",
        maxnum: "1111111",
        maxconcurrency: "11111111",
      },
    };
  },
  //创建后
  created() {},
  //挂载后
  mounted() {
    this.getsuperesmen();
  },
  //事件监听器
  methods: {
    sharing(val) {
      this.zhunagt = val;
      this.$emit("sharing", this.getsuperesdata[this.zhunagt]);
    },
    //添加
    submitForm(val) {
      this.$refs[val].validate((valid) => {
        if (valid) {
          addsuper(this.form).then((res) => {
            if (res.msg == "超级企业添加成功") {
              this.$message({
                message: res.msg,
                type: "success",
              });
              this.dialogFormVisible = false;
              this.getsuperesmen();
            } else {
              this.$message({
                message: res.msg,
                type: "error",
              });
            }
          });
        } else {
          this.$message({
            message: "请正确输入表单",
            type: "error",
          });
          return false;
        }
      });
    },
    //获取数据
    getsuperesmen() {
      getsuperes({}).then((res) => {
        // console.log(res.data.data);
        this.getsuperesdata = res.data.data;
        this.sharing(this.zhunagt);
      });
    },
    //搜索
    getsearchesmen() {
      getsearches({ ename: this.input }).then((res) => {
        // console.log(res);
        this.getsuperesdata = res.data.data;
      });
    },
  },
};
</script>

<style scoped lang="less">
.EnterpriseSearchinput {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
#EnterpriseSearch {
  padding: 5px;
  width: 200px;
  background-color: #fff;
  border-bottom: 1px solid #eee;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-bottom: 20px;
  p {
    font-size: 15px;
    font-weight: 800;
    color: #333;
  }
}
#SuperEnterpriseList::-webkit-scrollbar {
  width: 2px;
}
#SuperEnterpriseList::-webkit-scrollbar-thumb {
  background: #e9e9e9;
}

#SuperEnterpriseList {
  display: flex;
  flex-direction: column;
  width: 200px;
  background-color: #fff;
  height: 76vh;
  overflow: auto;

  ul {
    height: 90vh;
    li {
      display: flex;
      padding: 10px;
      border-bottom: 1px solid #eee;
      font-size: 15px;
      height: 50px;
      color: #979797;
      white-space: nowrap;
      overflow: hidden;
      span {
        font-size: 15px;
        font-weight: 800;
        margin-right: 20px;
      }
      &:hover {
        background-color: #f5f5f5;
      }
    }
  }
}
</style>
